.footer {
  background-color: #fff5f6;
  &__border {
    display: block;
    width: 100%;
    min-width: 320px;
    margin-left: -20px;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-bottom-color: #e1e1e1;

    @media screen and (min-width: 768px) {
      min-width: 768px;
    }
  }
}

.tablet-desktop-box {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
  }
}

.desc {
  margin-right: 66px;
  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    max-width: 340px;
  }

  &__text {
    @media screen and (max-width: 767px) {
      margin-bottom: 16px;
    }

    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-transform: uppercase;

    color: #ffa5ba;

    @media screen and (min-width: 768px) {
      font-size: 26px;
      line-height: 1.15;
      letter-spacing: 0.04em;

      &:first-child {
        margin-bottom: 12px;
      }
    }

    &--large {
      font-size: 34px;
      line-height: 1.15;
      letter-spacing: 0.04em;

      color: #d41443;

      @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 1.15;
        letter-spacing: 0.06em;
      }
    }
  }
}

.contacts-box__link {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.86;

  color: #000000;
}

.contacts-box__text--red {
  color: #d41443;
}

