.copyright {
  padding-top: 16px;
  padding-bottom: 20px;

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  &__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    letter-spacing: 0.02em;
    text-align: center;

    color: #907e82;

    @media screen and (min-width: 768px) {
      line-height: 1.86;
    }
  }
}
