.container {

  max-width: 320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;

  @media screen and (min-width: 768px) {

    max-width: 768px;
    padding-right: 18px;
  padding-left: 18px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
    padding-right: 16px;
    padding-left: 16px;

}
}